/* Page background */

.login-page {
    min-height: 100vh;
    background: linear-gradient(135deg,
            rgb(14, 25, 26) 0%,
            rgb(8, 9, 12) 50%,
            rgb(14, 25, 26) 100%);
    color: #e5e7eb;
    position: relative;
    overflow: hidden;
}

/* Floating Particles */
.particles {
    position: absolute;
    inset: 0;
}

.particle {
    position: absolute;
    background: #34d399;
    border-radius: 50%;
    filter: blur(1px);
}

/* Container */
.login-box {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 24px;
}

.logo img {
    width: 50%;
    margin: 0 auto;
    filter: drop-shadow(0 0 20px rgba(16, 185, 129, 0.6));
    margin-bottom: 3rem;
}

.logo img {
    width: 50%;
    margin: 0 auto;
    display: block;
    filter: drop-shadow(0 0 20px rgba(16, 185, 129, 0.6));
    margin-bottom: 3rem;
    transition: transform 0.3s ease, filter 0.3s ease;
}

.logo img:hover {
    transform: scale(1.2);
    /* makes it bigger */
    filter: drop-shadow(0 40px 40px 40px rgba(9, 255, 173, 0.9));
}

/* Login card */
.login-card {
    position: relative;
    width: 100%;
    max-width: 400px;
    background: linear-gradient(270deg,
            rgba(14, 25, 26, 0.884),
            rgba(8, 9, 12, 0.6),
            rgba(14, 25, 26, 0.8));
    background-size: 600% 600%;
    animation: gradientWave 4s ease infinite;

    /* Glass effect */
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);

    /* Glow + soft border */
    border-radius: 16px;
    padding: 38px;
    color: #fff;
    overflow: hidden;
}

/* Animate gradient movement */
@keyframes gradientWave {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}

.login-card::before,
.login-card::after {
    content: "";
    position: absolute;
    width: 10px;
    height: 15px;
    box-shadow: 0 0 30px 10px rgba(34, 112, 86, 0.6);
}

/* Top-left corner */
.login-card::before {
    top: -16px;
    left: -9px;
}

/* Top-right corner */
.login-card::after {
    top: -10px;
    right: -10px;
}

.login-card .corner-bottom-left,
.login-card .corner-bottom-right {
    content: "";
    position: absolute;
    width: 10px;
    height: 15px;
    box-shadow: 0 0 30px 10px rgba(34, 112, 86, 0.6);
}

/* Bottom-left corner */
.login-card .corner-bottom-left {
    bottom: -10px;
    left: -10px;
}

/* Bottom-right corner */
.login-card .corner-bottom-right {
    bottom: -10px;
    right: -10px;
}

/* Neon corners */
.corner {
    position: absolute;
    width: 48px;
    height: 48px;
}

.corner.tl {
    top: 0;
    left: 0;
    border-left: 2px solid rgba(16, 185, 129, 0.4);
    border-top: 2px solid rgba(16, 185, 129, 0.4);
    border-top-left-radius: 16px;
}

.corner.tr {
    top: 0;
    right: 0;
    border-right: 2px solid rgba(16, 185, 129, 0.4);
    border-top: 2px solid rgba(16, 185, 129, 0.4);
    border-top-right-radius: 16px;
}

.corner.bl {
    bottom: 0;
    left: 0;
    border-left: 2px solid rgba(16, 185, 129, 0.4);
    border-bottom: 2px solid rgba(16, 185, 129, 0.4);
    border-bottom-left-radius: 16px;
}

.corner.br {
    bottom: 0;
    right: 0;
    border-right: 2px solid rgba(16, 185, 129, 0.4);
    border-bottom: 2px solid rgba(16, 185, 129, 0.4);
    border-bottom-right-radius: 16px;
}

/* Header */
.login-header {
    text-align: center;
    margin-bottom: 24px;
}

.secure-badge {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    padding: 6px 14px;
    border: 1px solid rgba(16, 185, 129, 0.4);
    border-radius: 9999px;
    margin-bottom: 12px;
    font-size: 12px;
}

.secure-badge .icon {
    width: 16px;
    height: 16px;
    color: #2efdb1;
    animation: pulse 1.5s infinite;
}

/* Dots container */
.dots {
    display: flex;
    gap: 4px;
    margin-left: 6px;
}

/* Single dot */
.dot {
    width: 6px;
    height: 6px;
    background-color: #2efdb1;
    border-radius: 50%;
    opacity: 0.3;
    animation: blink 1.5s infinite;
}

/* Animate brightness one by one */
.dot:nth-child(1) {
    animation-delay: 0s;
}

.dot:nth-child(2) {
    animation-delay: 0.3s;
}

.dot:nth-child(3) {
    animation-delay: 0.6s;
}

@keyframes blink {

    0%,
    80%,
    100% {
        opacity: 0.3;
    }

    40% {
        opacity: 1;
    }
}

.login-header p {
    font-size: 12px;
    color: #9ca3af;
}

/* Form */
.login-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.form-group label {
    display: flex;
    align-items: center;
    gap: 6px;
    /* font-size: 0.875rem; */
    font-weight: 500;
    color: #d1d5db;
    margin-bottom: 6px;
    font-size: 13px;
}

.form-group input {
    width: 100%;
    padding: 12px 16px;
    border-radius: 8px;
    background: #11161a;
    border: 1px solid rgb(153 255 217 / 40%);
    /* color: #585858; */
    outline: none;
    font-size: 12px;
    margin-bottom: 10px;
}

.form-group input:focus {
    border-color: #34d399;
    box-shadow: 0 0 0 2px rgba(16, 185, 129, 0.5);
}

.password-wrapper {
    position: relative;
}

.toggle-password {
    position: absolute;
    top: 40%;
    right: 12px;
    transform: translateY(-50%);
    background: none;
    border: none;
    color: #9ca3af;
    cursor: pointer;
}

.toggle-password:hover {
    color: #34d399;
}

/* Error */
.error-msg {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 12px;
    border: 1px solid rgba(239, 68, 68, 0.3);
    background: rgba(239, 68, 68, 0.1);
    border-radius: 8px;
    color: #f87171;
    font-size: 0.875rem;
}

.form-icon {
    width: 16px;
    height: 16px;
    color: #2efdb1;
    /* animation: pulse 1.5s infinite; */
}

/* Button */
.login-btn {
    width: 100%;
    height: 48px;
    border-radius: 8px;
    background: linear-gradient(to right, #34d399, #10b981, #059669);
    color: #000;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    box-shadow: 0 0 20px rgba(16, 185, 129, 0.5);
    cursor: pointer;
    border: none;
    transition: opacity 0.2s ease-in-out;
    margin-top: 20px;
    font-size: 12px;
}

.login-btn-icon {
    width: 15px;
}

.login-btn:hover {
    opacity: 0.9;
}

.login-btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
}

.dropdown-boolen {
    display: flex;
    justify-content: space-between;
}






