/* ------------------------------ */
/* Layout */
.layout {
    display: flex;
    height: 100vh;
    background: #1f2937;
    /* dark gray */
    color: white;
    font-family: Arial, sans-serif;
    overflow: hidden;
}

/* Sidebar */
.sidebar {
    width: 250px;
    min-width: 250px;
    background: #0a0b0e;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-right: 1px solid #00d4aa33;
    transition: width 0.3s ease-in-out;
    height: 100vh;
    /* ensures full height */
    position: fixed;
    /* fixed sidebar */
    top: 0;
    left: 0;
}

/* Collapsed sidebar */
.sidebar.collapsed {
    width: 80px;
    min-width: 80px;
}

.sidebar.collapsed~.main {
    margin-left: 80px;
}

.sidebar-header {
    display: flex;
}

.shrinked {
    display: none;
    align-items: center;
    padding: 20px;
}

.notshrinked {
    padding: 20px;
    padding-right: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.collapse-btn {
    padding: 10px;
    padding-left: 0px;
    margin: auto;
    color: #00d4aa;
}

/* Logo */
.sidebar-logo {
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Menu Container */
.menu-bar {
    flex: 1;
    border-top: 1px solid #00d4aa33;
    border-bottom: 1px solid #00d4aa33;
    padding: 10px;
    overflow-y: auto;
    /* scroll if items overflow */
}

/* Menu */
.menu {
    display: flex;
    flex-direction: column;
    gap: 6px;
}

.menu button {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 5px 8px;
    border: none;
    background: transparent;
    color: #d1d5db;
    /* gray-300 */
    font-size: 12px;
    border-radius: 8px;
    cursor: pointer;
    transition: 0.3s;
    font-weight: 600;
    text-align: left;
    width: 100%;
}

.menu button:hover {
    background: rgba(16, 185, 129, 0.1);
    /* emerald-500/10 */
    color: #00d4aa;
}

.menu button.active {
    background: #00d4aa;
    color: #000;
}

.submenu-active {
    background: rgba(17, 153, 108, 0.1) !important;
    /* emerald-500/10 */
    color: #00d4aa !important;
    border: 1px solid #00d4aa !important;
}

.menu-icon {
    color: #00d4aa;
    font-size: 18px;
}

.menu button.active .menu-icon {
    color: black;
    /* icon becomes black */
}

.submenu-open button {
    opacity: 1;
    transform: translateX(0);
}

.submenu {
    padding-left: 20px;
    display: flex;
    flex-direction: column;
    gap: 6px;
    margin-top: 5px;
}

.submenu button {
    background: none;
    border: none;
    text-align: left;
    padding: 6px 10px;
    color: #bbb;
    font-size: 14px;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 6px;
    font-weight: 200;

}

.submenu button.active {
    color: #00f7d2;
    font-weight: bold;
}

.submenu-open {
    max-height: 500px;
    /* large enough to fit submenu */
}

/* Buttons default state */
.submenu button {
    opacity: 0;
    transform: translateX(-20px);
}

/* Animate in when submenu opens */
.submenu-open button {
    animation: slideIn 0.3s forwards;
}

/* Stagger effect */
.submenu-open button:nth-child(1) {
    animation-delay: 0.05s;
}

.submenu-open button:nth-child(2) {
    animation-delay: 0.1s;
}

.submenu-open button:nth-child(3) {
    animation-delay: 0.15s;
}

.submenu-open button:nth-child(4) {
    animation-delay: 0.2s;
}

.submenu-open button:nth-child(5) {
    animation-delay: 0.25s;
}

.submenu-open button:nth-child(6) {
    animation-delay: 0.3s;
}

.submenu-open button:nth-child(7) {
    animation-delay: 0.35s;
}

.submenu-open button:nth-child(8) {
    animation-delay: 0.4s;
}

.submenu-open button:nth-child(9) {
    animation-delay: 0.45s;
}

/* Keyframes for sliding effect */
@keyframes slideIn {
    from {
        opacity: 0;
        transform: translateX(-20px);
    }

    to {
        opacity: 1;
        transform: translateX(0);
    }
}

