/* -------------popup------------------ */

/* Overlay */
.modal-overlay {
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(4px);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
}

/* Modal Box */
.modal-box {
    background: #0f131d;
    border: 1px solid #1f2737;
    border-radius: 12px;
    padding: 24px;
    width: 420px;
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.6);
    color: #d1d5db;
    font-family: Arial, sans-serif;
}

/* Title */
.modal-title {
    color: #00d4aa;
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 8px;
}

/* Message */
.modal-message {
    font-size: 12px;
    margin-bottom: 16px;
    color: #c7c7c7;
}

/* Label */
.modal-label {
    display: block;
    font-size: 12px;
    color: #9ca3af;
    margin-bottom: 6px;
}

/* Textarea */
.modal-textarea {
    width: 100%;
    background: #0c0f16;
    border: 1px solid #1f2737;
    border-radius: 6px;
    color: #e5e7eb;
    padding: 8px 10px;
    font-size: 12px;
    resize: none;
    margin-bottom: 20px;
}

.modal-textarea:focus {
    outline: none;
    border-color: #00d4aa;
    box-shadow: 0 0 6px #00d4aa55;
}

/* Actions */
.modal-actions {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
}

/* Cancel Button */
.btn-cancel {
    padding: 8px 16px;
    border: 1px solid #1f2737;
    background: #0c0f16;
    color: #d1d5db;
    border-radius: 6px;
    font-size: 12px;
    cursor: pointer;
    transition: 0.2s;
}

.btn-cancel:hover {
    border-color: #00d4aa;
    color: #00d4aa;
}

/* Confirm Button */
.btn-confirm {
    padding: 8px 16px;
    border: none;
    background: #00d4aa;
