/* Overlay background */
.modal-overlay {
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.65);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
}

/* Container */
.modal-container {
    background: #0d0f13;
    padding: 20px 24px;
    border-radius: 8px;
    width: 500px;
    border: 1px solid #333;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.6);
    animation: scaleIn 0.2s ease-out;
}

/* Title */
.modal-title {
    color: #22c55e;
    /* green */
    font-size: 15px;
    font-weight: 600;
    margin-bottom: 6px;
}

/* Subtext */
.modal-subtext {
    color: #aaa;
    font-size: 13px;
    margin-bottom: 14px;
}

/* Label */
.modal-label {
    display: block;
    color: #ddd;
    font-size: 12px;
    margin-bottom: 6px;
}

/* Textarea */
.modal-textarea {
    width: 100%;
    min-height: 70px;
    padding: 8px 10px;
    background: #1a1f2e;
    color: #e0e0e0;
    border: 1px solid #444;
    border-radius: 6px;
    font-size: 12px;
    resize: none;
    margin-bottom: 16px;
}

.modal-textarea::placeholder {
    color: #666;
}

/* Footer */
.modal-footer {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
}

/* Buttons */
.btn-cancel {
    padding: 8px 16px;
    background: #333;
    color: #ddd;
    border: none;
    border-radius: 6px;
    font-size: 12px;
    cursor: pointer;
    transition: background 0.2s ease;
}

.btn-cancel:hover {
    background: #444;
}

.btn-approve {
    padding: 8px 16px;
    background: #22c55e;
    display: flex;
    gap: 10px;

    /* green */
    color: #000;
    border: none;
    border-radius: 6px;
    font-size: 12px;
    font-weight: 500;
    cursor: pointer;
    transition: background 0.2s ease;
}

.btn-approve:hover {
    background: #16a34a;
}

/* Animation */
@keyframes scaleIn {
    from {
        transform: scale(0.9);
        opacity: 0;
    }

    to {
        transform: scale(1);
        opacity: 1;
    }
}

.approval-back-button {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 4px 8px;
    background: rgba(13, 15, 19, 0.6);
    /* glassy dark bg */
    border: 1px solid rgba(0, 255, 200, 0.3);
    /* teal subtle border */
    color: #d1d5db;
    /* gray-300 */
    border-radius: 8px;
    cursor: pointer;
    font-size: 12px;
    font-weight: 500;
    backdrop-filter: blur(6px);
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
}

/* Icon size */
.approval-back-button .icon {
    width: 16px;
    height: 16px;
}

/* Glass wave hover effect */
.approval-back-button::before {
    content: "";
    position: absolute;
    top: 0;
    left: -100%;
    width: 200%;
    height: 100%;
    background: linear-gradient(120deg,
            transparent,
            rgba(0, 255, 200, 0.15),
            transparent);
    transform: skewX(-25deg);
    transition: left 0.6s ease;
}

.approval-back-button:hover::before {
    left: 100%;
}

.approval-back-button:hover {
    color: #fff;
    border-color: rgba(0, 255, 200, 0.6);
    background: rgba(20, 24, 30, 0.8);
}






/* ----------------------------------------------------------------- */

/* General Layout */
.container {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 20px;
    min-height: 100vh;
    color: #fff;
    /* padding: 20px; */
}

.left,
.right {
    display: flex;
    flex-direction: column;
    gap: 20px;

}

/* Card Styling */
.card {
    background: #0d0f13;
    border: 1px solid #222;
    border-radius: 16px;
    padding: 20px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
    border-color: #0B1E20;
}

.section-title {
    color: #00e6c3;
    font-weight: 200;
    margin-bottom: 15px;
}

.section-header {
    width: 100%;
    height: 40px;
    /* fixed header height */
    display: flex;
    align-items: center;

    margin-bottom: 8px;
}

/* Info Grid */
.info-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 25px;
    /* very small gap between rows */
    column-gap: 12px;
    /* keep columns spaced */
}

/* .info-block {
  margin-bottom: 4px; 
} */

.info-block {
    margin: 0;
    padding: 0;
    line-height: 0.2;
    /* compress height */

}

.info-block .label {
    font-size: 12px;
    color: #aaa;
    margin-bottom: 12px !important;
    font-weight: 600;
    margin: 0;
    /* remove default gap */
    line-height: 1.2;
}

.info-block .value {
    font-weight: 700;
    display: flex;
    align-items: center;
    font-size: 14px;


}

.cards .info-block {
    line-height: 0.6;
    margin-bottom: 22px;
}

/* Documents */
.doc-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 12px;
}

.doc-btn {
    background: #222;
    border: 1px solid #333;
    border-radius: 10px;
    padding: 10px;
    font-size: 14px;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    transition: background 0.2s;
}

.doc-btn:hover {
    background: #333;
}

.eye {
    color: #00e6c3;
    font-weight: bold;
}

/* Action Buttons */
.action-btn {
    width: 100%;
    padding: 12px;
    border-radius: 10px;
    font-weight: bold;
    color: white;
    border: none;
    cursor: pointer;
    margin-top: 10px;
    display: flex;
    justify-content: center;
    gap: 8px;
    transition: background 0.3s;
}

.blue {
    background: #005eff;
}

.blue:hover {
    background: #337dff;
}

.green {
    background: #00a86b;
}

.green:hover {
    background: #00c97a;
}

.red {
    background: #e63946;
}

.red:hover {
    background: #ff4d5a;
}

.row {
    display: flex;
    gap: 10px;
}

.note {
    font-size: 12px;
    color: #999;
    margin-top: 10px;
}

.doc-btn svg {
    color: #00e6c3;
    /* make all icons teal */
}

.up-section-title {
    display: flex;
    color: #00e6c3;
    align-items: center;
    justify-content: center;
    gap: 2px;
    font-size: 13px;
    margin-bottom: 15px;

}

.section-title-sub {
    display: flex;
    align-items: center;
    /* justify-content: center; */
    gap: 2px;
    font-size: 16px;
    color: #00e6c3;
    font-weight: 200;
    margin-bottom: 15px;
}

.refresh-icon {
    margin-right: 6px;
}

.note-box {
    border: 1px solid #0B1E20;
    /* border color */
    border-radius: 12px;
    /* rounded corners */
    padding: 10px 15px;
    text-align: center;
    /* center text */
    margin-top: 10px;
    /* optional light background */
}

.up-note {
    color: #aaa;
    /* text color */
    margin: 0;
    /* remove default spacing */
    font-size: 11px;
}

/* Responsive */
@media (max-width: 900px) {
    .container {
        grid-template-columns: 1fr;
    }

    .info-grid {
        grid-template-columns: 1fr;
    }

    .doc-grid {
        grid-template-columns: 1fr;
    }
}






